body {
    font-family: 'Arial', sans-serif;
    background-color: #f8f8f8;
}

.card-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin: auto;
    padding: 30px;
    max-width: 800px;
}

.card {
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.15);
}

.glow-red {
    animation: glowRed 0.5s ease;
}

.glow-green {
    animation: glowGreen 0.5s ease;
}

@keyframes glowRed {
    0% {
    box-shadow: none;
    }
    50% {
    box-shadow: 0 0 10px 5px red;
    }
    100% {
    box-shadow: none;
    }
}

@keyframes glowGreen {
    0% {
    box-shadow: none;
    }
    50% {
    box-shadow: 0 0 10px 5px lime;
    }
    100% {
    box-shadow: none;
    }
}

/* Score and Chosen Cards Container */
p {
    font-size: 1.2em;
    margin: 10px;
    color: #333;
}

h3 {
    margin: 10px;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}
